module.exports = "<!-- FORM -->\r\n\r\n<form #pharmeceuticalForm=\"ngForm\" autocomplete=\"off\">\r\n\r\n    <div class=\"col-sm-12 margin-bottom\" [class.btn-action-container]=\"booActive\">\r\n        <button md-raised-button color=\"secundary\" (click)=\"goList()\" class=\"btn-w-md no-margin-left\">Regresar</button>\r\n    </div>\r\n\r\n    <!-- name -->\r\n    <div class='col-sm-12 col-md-6  margin-bottom'>\r\n        <md-input-container class=\"full-width\">\r\n            <input required type=\"text\" id=\"name\" name=\"name\" [(ngModel)]=\"model.name\" mdInput placeholder=\"Nombre\">\r\n        </md-input-container>\r\n    </div>\r\n    <!-- estado -->\r\n    <div class='col-sm-12 col-md-6 margin-bottom'>\r\n        <div class=\"flex\">\r\n            <span class=\"flex-spacer\"></span>\r\n            <md-slide-toggle [labelPosition]=\"'before'\" id=\"state\" name=\"state\" [(ngModel)]=\"model.state\">Estado</md-slide-toggle>\r\n        </div>\r\n    </div>\r\n    <!-- Principio activo -->\r\n    <div class=\"col-sm-12 margin-bottom\">\r\n        <div class=\"row\">\r\n            <div class=\"col-sm-12\">\r\n                <div class=\"flex margin-bottom\">\r\n                    <div class=\"box-header no-padding-h\">Principio activo</div>\r\n                    <span class=\"flex-spacer\"></span>\r\n                    <button md-raised-button color=\"primary\" class=\"btn-w-md no-margin-left\" (click)=\"openModalActiveIngredients()\">Añadir</button>\r\n                </div>\r\n                <div class=\"box box-default table-box table-responsive mdl-shadow--2dp\">\r\n                    <table class=\"mdl-data-table table-bordered table-striped no-margin\">\r\n                        <thead>\r\n                            <tr>\r\n                                <th>Nombre</th>\r\n                                <th>Concentración</th>\r\n                                <th>Unidad de medida</th>\r\n                            </tr>\r\n                        </thead>\r\n                        <tbody>\r\n                            <tr *ngFor=\"let item of arrActive_ingredients; let index = index\">\r\n                                <td>{{item.name}}</td>\r\n                                <td>\r\n                                    <md-input-container class=\"full-width\">\r\n                                        <input type=\"text\" id=\"concentration{{index}}\" name=\"concentration{{index}}\" [(ngModel)]=\"item.concentration\" mdInput>\r\n                                    </md-input-container>\r\n                                </td>\r\n                                <td>\r\n                                    <md-select class=\"full-width\" [(ngModel)]=\"item.measurement_unit_id\" name=\"measurement_unit_id{{index}}\" id=\"measurement_unit_id{{index}}\">\r\n                                        <md-option *ngFor=\"let item of arrMeasurement_unit\" [value]=\"item.id\">{{item.value.trim()}}</md-option>\r\n                                    </md-select>\r\n                                </td>\r\n                            </tr>\r\n                        </tbody>\r\n                    </table>\r\n                </div>\r\n            </div>\r\n        </div>\r\n    </div>\r\n    <!-- dosage_form_id -->\r\n    <div class='col-sm-12 col-md-4 col-lg-3 margin-bottom'>\r\n        <md-select class=\"full-width\" placeholder=\"Forma Farmacéutica\" [(ngModel)]=\"model.dosage_form_id\" name=\"dosage_form_id\" id=\"dosage_form_id\">\r\n            <md-option *ngFor=\"let i of pharmaceutical_form\" [value]=\"i.id\">{{i.value}}</md-option>\r\n        </md-select>\r\n    </div>\r\n    <!-- routes_administration_id -->\r\n    <div class='col-sm-12 col-md-4 col-lg-3 margin-bottom'>\r\n        <md-select class=\"full-width\" placeholder=\"Vía de administración\" [(ngModel)]=\"model.routes_administration_id\" name=\"routes_administration_id\"\r\n            id=\"routes_administration_id\">\r\n            <md-option *ngFor=\"let i of routes_administration\" [value]=\"i.id\">{{i.value}}</md-option>\r\n        </md-select>\r\n    </div>\r\n    <!-- atc -->\r\n    <div class='col-sm-12 col-md-4 col-lg-3 margin-bottom'>\r\n        <md-input-container class=\"full-width\">\r\n            <input required type=\"text\" id=\"atc\" name=\"atc\" [(ngModel)]=\"model.atc\" mdInput placeholder=\"Código ATC\">\r\n        </md-input-container>\r\n    </div>\r\n    <!-- regulated_price -->\r\n    <div class='col-sm-12 col-md-4 col-lg-3 margin-bottom'>\r\n        <md-input-container class=\"full-width\">\r\n            <input required type=\"text\" id=\"regulated_price\" name=\"regulated_price\" [(ngModel)]=\"model.regulated_price\" mdInput placeholder=\"Precio regulado\">\r\n        </md-input-container>\r\n    </div>\r\n    <!-- storage_condition_id -->\r\n    <div class='col-sm-12 col-md-3 margin-bottom'>\r\n        <md-select class=\"full-width\" placeholder=\"Almacenamiento\" [(ngModel)]=\"model.storage_condition_id\" name=\"storage_condition_id\"\r\n            id=\"storage_condition_id\">\r\n            <md-option *ngFor=\"let i of storage_condition\" [value]=\"i.id\">{{i.value}}</md-option>\r\n        </md-select>\r\n    </div>\r\n    <!-- is_controlled -->\r\n    <div class='col-sm-12 col-md-3 margin-bottom'>\r\n        <md-slide-toggle [labelPosition]=\"after\" id=\"is_controlled\" name=\"is_controlled\" [(ngModel)]=\"model.is_controlled\">Controlado</md-slide-toggle>\r\n    </div>\r\n    <!-- is_monopoly -->\r\n    <div class='col-sm-12 col-md-3 margin-bottom'>\r\n        <md-slide-toggle [labelPosition]=\"after\" id=\"is_monopoly\" name=\"is_monopoly\" [(ngModel)]=\"model.is_monopoly\">Monopolio</md-slide-toggle>\r\n    </div>\r\n    <!-- is_pos -->\r\n    <div class='col-sm-12 col-md-3 margin-bottom'>\r\n        <md-slide-toggle [labelPosition]=\"after\" id=\"is_pos\" name=\"is_pos\" [(ngModel)]=\"model.is_pos\">POS</md-slide-toggle>\r\n    </div>\r\n    <div class=\"col-sm-12\" [class.btn-action-container]=\"booActive\">\r\n        <button [disabled]=\"action_active ? false : !pharmeceuticalForm.form.valid\" md-raised-button color=\"primary\" class=\"btn-w-md no-margin-left btn-right\"\r\n            (click)=\"save()\">{{str_action}}</button>\r\n    </div>\r\n</form>"

